/*
Gestion scrollbar
*/

@tailwind utilities;


.container-animation-maskon{
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 9999;
  overflow: hidden;
  background-color: #ffffff;
}

.container-animation {
  overflow: hidden;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  z-index: 9999;
  background-color: #ffffff;
  animation: expand 0.5s ease forwards;
  transition: all 0.5s ease;
}

@keyframes expand {
  0% {
    opacity: 100%;
  }
  100% {
    opacity: 0%;
    visibility: hidden;
  }
}

.btn-primary-kdds {
