.btneffect {
  font-size: 12px;
  position: relative;	
  padding: 1rem 1.2rem;
  padding-right: 1.1rem;
  color: rgb(0, 0, 0);
  transition: all 500ms cubic-bezier(0.77, 0, 0.175, 1);	
  cursor: pointer;
  user-select: none;
}

.btneffect:before, .btneffect:after {
  content: '';
  position: absolute;	
  transition: inherit;
}

.btneffect:hover {
  color: rgb(173, 0, 0);
  transition-delay: .5s;
}

.btneffect:hover:before {
  transition-delay: 0s;
}

.btneffect:hover:after {
  background: rgba(255, 0, 0, 0);
  transition-delay: .35s;
  z-index: -1;
}

/* From center */

.from-center:before {
  top: 0;
  left: 50%;
  height: 100%;
  width: 0;
  border: 1px solid rgb(180, 0, 0);
  border-left: 0;
  border-right: 0;
}

.from-center:after {
  bottom: 0;
  left: 0;
  height: 0;
  width: 100%;
  background: rgba(255, 255, 255, 0);
}

.from-center:hover:before {
  left: 0;
  width: 100%;
}

.from-center:hover:after {
  top: 0;
  height: 100%;
}

