  line-height: normal;
  width: 200px;
  height: 40px;
  border-radius: 5px;
  background: #FFF;
  cursor: pointer;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25) inset;
}
.rctitleSAV{
  top: 0px;
  color: #00618a;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  line-height: normal;
  transition-duration: 500ms;
}

.rootCardSAV:after {
  position: absolute;
  transition: 0.3s;
  content: "";
  width: 0;
  left: 0;
  bottom: 0;
  height: 100%;
  background: #00618a;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}

.rootCardSAV:hover:after {
  width: 100%;
  left: 0;
}

.rootCardSAV:hover .rctitleSAV {
  z-index: 10;
  color: #ffffff;
  transition-duration: 500ms;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

